.todo {
  display: flex;
  align-items: center;
  color: #444;
  padding: 10px 20px;
  width: 100%;
  background-color: #fff;
  border-radius: 5px;
  border: 2px solid #222;
  margin-bottom: 15px;
}

.todoText {
  width: 100%;
  text-align: left;
}

.todoIcon {
  font-size: 1.8rem;
  color: teal;
  margin-right: 20px;
}

.complitedTodo {
  background-color: unset;
  border-color: gray;
  color: gray;
}

.checkIcon,
.deleteIcon {
  cursor: pointer;
  color: lightgrey;
  padding: 0 7px;
  font-size: 1.8rem;
  transition: transform 0.3s;
}

.todo.complitedTodo .checkIcon,
.todo.complitedTodo .todoIcon,
.todo.complitedTodo .deleteIcon {
  color: gray;
}

.checkIcon:hover,
.deleteIcon:hover {
  transform: scale(1.3);
}

.checkIcon:hover {
  color: green;
}

.deleteIcon:hover {
  color: red;
}
